/* =error404
----------------------------------------------- */

.error404 {
    width: 75%;
}

#under, .under {
    background: #fff;
    text-align: center;
    padding: 5px 0px;
    bottom: 10px;
    left: 50%;
    margin-left: -250px;
    right: 10px;
    z-index: 10;
    font-size: 0.8em;
    position:fixed;
    z-index: 10000;
    
    width: 600px;
    overflow: hidden;
    
    box-shadow: 0px 1px 6px rgba(0,0,0,0.3);
    border-radius: 2px;
    background: rgba(0,0,0,0.8);
    color: #fff;
    box-sizing: border-box;
    
    a{
        float:right;
        margin-right: 10px;
        display: block;
        background: #fff;
        color: black;
        border-radius: 100px;
        cursor: pointer;
        height: 25px;
        width: 25px;
        line-height: 25px;
        box-sizing: border-box;
        text-align: center;
        
         @media (max-width: $xs) {
             margin-right: -60px;
             margin-top: -20px;
             height: 35px;
            width: 35px;
            line-height: 35px;
        }
    }
    .line{
        position: absolute;
        display: block;
        width: 0px;
        height: 2px;
        background: #fff;
        left: 0px;
        bottom: 0px;
        transition: all ease 38000ms;
        @media (max-width: $xs) {
            width: 0%;
        }
    }
    .line.countdown{
        width: 600px;
        @media (max-width: $xs) {
            width: 100%;
        }
    }
     @media (max-width: $xs) {
        font-size: 0.5em;
         padding-right: 70px;
         padding-left: 10px;
         text-align: left;
         width: 95%;
         left: 2.5%;
         margin-left: 0px;
    }
}

.revealOnScroll {
    opacity: 0;
    &.animated {
        opacity: 1;
    }
}


/* =Loader
----------------------------------------------- */

.lcontainer {
    display: none;
}

.lcontainer.show {
    display: block;
}

.pageload-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    visibility: hidden;
    z-index: 100;
}

.pageload-overlay.show {
    visibility: visible;
}

.pageload-overlay svg {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
}

.pageload-overlay svg path {
    fill: $bg;
}

.pageload-overlay.preloading {
    background: $bg;
}

.pageload-overlay::after,
.pageload-overlay::before {
    content: '';
    position: fixed;
    width: 20px;
    height: 20px;
    top: 50%;
    left: 50%;
    margin: -10px 0 0 -10px;
    border-radius: 50%;
    visibility: hidden;
    opacity: 0;
    z-index: 1000;
    -webkit-transition: opacity 0.15s, visibility 0s 0.15s;
    transition: opacity 0.15s, visibility 0s 0.15s;
}

.pageload-overlay::after {
    background: $text-gold;
    -webkit-transform: translateX(-20px);
    transform: translateX(-20px);
    -webkit-animation: moveRight 0.6s linear infinite alternate;
    animation: moveRight 0.6s linear infinite alternate;
}

.pageload-overlay::before {
    background: $accent;
    -webkit-transform: translateX(20px);
    transform: translateX(20px);
    -webkit-animation: moveLeft 0.6s linear infinite alternate;
    animation: moveLeft 0.6s linear infinite alternate;
}

@-webkit-keyframes moveRight {
    to {
        -webkit-transform: translateX(20px);
    }
}

@keyframes moveRight {
    to {
        transform: translateX(20px);
    }
}

@-webkit-keyframes moveLeft {
    to {
        -webkit-transform: translateX(-20px);
    }
}

@keyframes moveLeft {
    to {
        transform: translateX(-20px);
    }
}

.pageload-loading.pageload-overlay::after,
.pageload-loading.pageload-overlay::before {
    opacity: 1;
    visibility: visible;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}